import React from 'react'
import { NavLink } from 'react-router-dom'
import HeaderContainer from './Header/HeaderContainer'
import s from './TopNavigation.module.css'
import './TopNavigationActive.css'

const TopNavigation = props => {
    // console.log(props.authCook.userId)
    let home = `/Home/${props.authCook.userId}`
    return(
        <header className={s.header}>
       <HeaderContainer />
      <NavLink exact to={home} className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_home_page_100px.png')} alt='Home'/></NavLink>
      <NavLink exact to='/Hot_Offers' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_fire_100px.png')} alt='Hot offers'/></NavLink>
      <NavLink exact to='/Message' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_speaker_notes_100px.png')} alt='Message'/></NavLink>
      <NavLink exact to='/Offers' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_job_seeker_100px.png')} alt='Offer'/></NavLink>
      <NavLink exact to='/Payment' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_atm_100px.png')} alt='Payment'/></NavLink>
      <NavLink exact to='/Gallery' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_picture_100px.png')} alt='Gallery'/></NavLink>
      <NavLink exact to='/Search' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_search_100px.png')} alt='Search'/></NavLink>
      <NavLink exact to='/Top_Users' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_crown_100px.png')} alt='Top users'/></NavLink>
      <NavLink exact to='/Settings' className={s.LinkHeader}><img src={require('../../img/header_icon/icons8_job_100px.png')} alt='Settings'/></NavLink>
      <div className={s.line_header}></div>
        </header>
    )
}
export default TopNavigation