import React, { useState } from 'react'
import s from './MessageDialogs.module.css'

const MessageDialogs = ({
    UserMess=[],
    newMessageText,
    P_F_SendNewMessagge,
    P_F_Message_newMessageText,
    id
}) => {
    const [InputTextValue, setInputTextValue] = useState(newMessageText)


const F_InputTextValue = e => { 
    setInputTextValue(e.target.value)
  P_F_Message_newMessageText(e.target.value, id)
    
}
const F_SendNewMessagge = () => {
    P_F_SendNewMessagge(id)
    setInputTextValue('')
}
// console.log(InputTextValue.trim());
let messages = UserMess.map(mes=> (<div className={s.iteMessMy} key={mes.id}>{mes.message}</div>));
    return(
   <div className={s.MainMessageDialogs}>
       
       <div className={s.allthemessage}>
       <div className={s.anotherUser}>new</div>
       {messages}
       <div className={s.anotherUser}>new</div>
       </div>
     
    <div className={s.sendMessage}>

<input
type='text'
className={s.inputText}
onChange={F_InputTextValue} 
   value={InputTextValue} 
/>
{InputTextValue.trim()
 ?  <button className={s.button}
onClick={F_SendNewMessagge}
>Send</button>
: <button className={s.button +' '+ s.false_b}>Send</button>
}

</div>
</div>
    )
}
export default  MessageDialogs

