import React, { useRef } from 'react'
import s from './Profile.module.css'
import imgUser from '../../../img/imgUser/User_Img.jpg'
import { NavLink } from 'react-router-dom'
import ProfileStatus from './ProfileStatus'

const Profile= ({data,updateStatus,status}) => {

const avatar = data.photos?.large ? data.photos.large : imgUser
let ref  = useRef(null)
const fun_Ref =()=> {
    ref.current.focus()
    // console.log(ref);
}

    return(
        <div className={s.container_profile}>
  <img src={avatar} alt="avatar" className={s.img} ref={ref}/>
  <ProfileStatus status={status} updateStatus={updateStatus}/>

        </div>
    )
}
export default Profile