// HeaderContainer
import React from 'react'
import s from './HeaderContainer.module.css'
import { NavLink } from 'react-router-dom'
import login from '../../../img/logo/login.png'

const Header = ({authCook}) => {
    return (
        <div className={s.LoginBlock}>
            {authCook.isAuth ? authCook.login : <NavLink to={'/Login'}><img src={login} /></NavLink>}
            {/* <NavLink to={'/Login'}><img src={login}/> </NavLink> */}
    </div>
    )
}
export default Header