import React, { memo, useState } from 'react'
import S from '../Offers.module.css'
const Offer = props => {
const [data, setData] = useState(props.of.value)  
const [isCorrect, setIsCorrect] = useState(false)


const onChange_value = (e) => {
  setData(e.target.value)
}
const onBlur_value = () => {
  setIsCorrect(false)
  props.onblur(data, props.of.id)
 
}
    return <>
    <li className={S.active}  id={props.of.id}>
    <input type="checkbox" className={S.checked_i} onClick={()=>props.boolPacked(props.of.id)}  defaultChecked={props.of.packed} />
   {isCorrect 
   ?  <input autoFocus={true} onBlur={onBlur_value} onChange={onChange_value} value={data} /> 
   :  <span onDoubleClick={()=>{setIsCorrect(true)}}> {data}</span>}
 
    
    
        
        <button className={S.clossed} onClick={()=>props.removeLI(props.of.id)}>X</button></li></>
}
export default memo(Offer)
//  onDoubleClick='ondblclickFun(this, {index})'