const HomeReduser = (state=stateHome, action) => {
    switch (action.type) {
        case D.ADD_NEW_POST:
        let newPost = {
            message: action.newText,
            likesCounts: 0,
            id: id(),
            img:state.data.photos.largea
        }
        return {
            ...state, 
            Posts: [...state.Posts,newPost]
        }
        case D.H_OPEN_USER_HOME_PAGE: 
          return{
           ...state, data: action.data
          }
          case D.S_LOADING_FALSE :
            return{
                ...state, isLoading: false
            }
        case D.S_LOADING_TRUE :
            return{
                ...state, isLoading: true
            }
        case D.SET_STATUS :
          return {
            ...state,
            status: action.status

          }
        default: return state;
    }

}

export default HomeReduser


