import React, { useState } from 'react'
import s from './Myposts.module.css'
import Post from './Post/Post'


const Myposts= ({
    Posts,
    R_FU_HOME_addNewPostRed
}) => {

const [textareaValue, setTextareaValue] = useState('')
let [textareaplaceholder, setTextareaplaceholder] = useState('');
 

const F_UtextareaValue = e=> {
setTextareaValue(e.target.value)
}

const FUaddNewPost = () =>{
    if(textareaValue.replace(/ +/g, ' ').trim() !== '') {
        setTextareaplaceholder('')
        R_FU_HOME_addNewPostRed(textareaValue)

        setTextareaValue('')
    } else {
        setTextareaplaceholder('No the text');
    }
}

const keyPressHandle = e => {
if(e.key === 'Enter') {
    FUaddNewPost()
}
}

let alltheposts = Posts.map(post=><Post key={post.id} Post={post} UserImg={'https://demiart.ru/forum/journal_uploads13/j2406363_1591724080_0.jpg'}/>);  

    return(
        <div>
            {/* <div> */}
                <div className={s.container_imput}>
                <input
                type='text' 
                placeholder={textareaplaceholder}
                value={textareaValue} 
                onChange={F_UtextareaValue} 
                className={s.post_input}
                onKeyPress={keyPressHandle}/>
                {/* </div> */}
            <button className={s.button} onClick={FUaddNewPost}>Add post</button>
            </div> 
            <div className={s.container_post}>
            {alltheposts}
            </div>
        </div>
    )
}

export default  Myposts