import React, { useEffect, useState } from 'react'
import SearchUser from './SearchUser/SearchUser'
import {generate as id} from "shortid"
import S from './Search.module.css'

const Search= props => {
console.log(props);
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);

    let pages = [];
    for(let i = 1; i <= pagesCount; i++){
    pages.push(i)
    }

const allthePage = pages.map(p =>{

// if(p<=28) {
    return(
        <span key={id()} 
        onClick={()=>{props.F_onPageChange(p)}} 
        className={props.currentPage === p ? S.spanPageNumber: null}
        >{p}</span>
   )
//   }
});
    let User = props.Users.map(u=>
    <SearchUser 
        key={u.id} 
        User={u} 
        followingPogres ={props.followingPogres}
        follow_Thank={props.follow_Thank}
        unfollow_Thank={props.unfollow_Thank}
         />)
    return(
        <div className={S.global_container}> 
            <div className={S.containerPage}>
                {allthePage}
            </div>
            <div className={S.containerUsers}>
            {/* className={S.spanPageNumber} */}
            {/* <button>Get users</button> */}
                  {User}
            </div>
        </div>
    )
}

export default Search












