import React from 'react'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux'


const LoginRedirect_HOC_ = Copmonents => props => {
    return !props.authCook.isAuth ?  <Redirect to={'/Login'}/> : <Copmonents  {...props}/>
    } 

    // const  mapStatetoProps = state => { return {authCook : state.authCook }}
    // let LoginRedirect_HOC = connect(mapStatetoProps)(LoginRedirect_HOC_)
export default  LoginRedirect_HOC_