import D from '../../DispatchAction'
import {generate as id} from "shortid"
import store from '../../state'

 const MessageReduser = (state=store._state.UserNameDontrepited.dataUser.messagePage.UserFriends, action) => {
  let stateCopy = [...state];
switch(action.type) {
   case D.M_NEW_MESSAGE:
  // stateCopy.find( i => i.id === action.id).newMessageText = action.newMessageText
  state.find( i => i.id === action.id).newMessageText = action.newMessageText
    return state
   case D.M_SEND_NEW_MESSAGE: 
  //  let findObj = state.find( i => i.id == action.id)
  // findObj.UserMess.push({message:findObj.newMessageText, id: id()})
  // findObj.newMessageText ='';
   let findObj = stateCopy.find( i => i.id === action.id)
  //  stateCopy.UserMess = [...state.find( i => i.id === action.id).UserMess]

   findObj.UserMess.push({message:findObj.newMessageText, id: id()})
  // findObj.UserMess = [...state.find( i => i.id === action.id).UserMess, {message:findObj.newMessageText, id: id()}]

   findObj.newMessageText ='';

   return stateCopy
   default: return state
 }
}
export default MessageReduser;








