import { generate as id } from "shortid";
const D = {
    CLOSED : 'CLOSED',
    ADD_TO: 'ADD_TO',
    CHECKED_TO: 'CHECKED_TO',
    CORRECT : 'CORRECT',
    CURRENT_CORRECT : 'CURRENT_CORRECT'
}
 export const  arrElem =[

        { value: "JavaScript", id: id(), packed: true, filter: false},
        { value: "HTML5", id: id(), packed: true, filter: false},
        { value: "CSS3", id:id(), packed: true, filter: false},
        { value: "SASS", id:id(), packed: true, filter: false},
        { value: "Regex", id:id(), packed: true, filter: false},
        { value: "Redux", id:id(), packed: true, filter: false},
        { value: "React", id:id(), packed: true, filter: false},
        { value: "TypeScript", id:id(), packed: false, filter: false},
        { value: "Python", id:id(), packed: false, filter: false},
        { value: "php", id:id(), packed: false, filter: false},
        { value: "mongoDB", id:id(), packed: true, filter: false},
        { value: "Jest", id:id(), packed: false, filter: false}
 ]

 const Todolist = (state=arrElem, action)=>{
        switch(action.type){
            case D.CLOSED :
                return  state.filter(i => action.id !== i.id)
           
            case D.CHECKED_TO :
                return state.map(i => {
                        action.id === i.id && (i.packed = !i.packed)
                        return i
                    })
            case D.ADD_TO:
               return [...state, {value: action.title, id: id(), packed: false, filter: false}]
            case D.CORRECT:
                console.log(state);
                return state.map(i=>{
                    action.id === i.id && (i.value = action.text)
                    return i
                })           
             default: return state
}
 } 


 export const removeLI = (id) =>({
    type: D.CLOSED, id
 })

export const boolPacked = (id) => ({
    type: D.CHECKED_TO, id
})
   
export const click_add = (title) => ({
    type: D.ADD_TO, title
})

export const onblur = (text,id) => ({
    type: D.CORRECT, text,id
})
 export default Todolist;