import React, { useState } from 'react'
import {setFormObj} from '../../Helps/setFormObj' 
import {Field, reduxForm} from "redux-form";

const formElem = {
    login: '',
    password: ''
}

const LoginContainer = (props) => {
    const [currentForm, setCurrentForm] = useState(formElem)

// console.log(LoginReduxForm);
        return <>
        <h1></h1>
        <form >
            <div><input type='text' name='login' id="login" value={currentForm.login} onChange={setFormObj(currentForm, setCurrentForm)} placeholder='Login'/></div>
            <div><input type='password' name='password' placeholder='Password'  value={currentForm.password} onChange={setFormObj(currentForm, setCurrentForm)}/></div>
            <div><input type='checkbox'/>what is your choice </div>
            <div><button>Login</button></div>
        </form>
               </>


}

