import D from '../DispatchAction'

let loading_state = {
    isLoading: false
}

const loading = (state=loading_state, action) => {
switch(action.type){
    case D.LOADING_FALSE :
        return{
            ...state, isLoading: false
        }
    case D.LOADING_TRUE :
        return{
            ...state, isLoading: true
        }
    default: return state
}
}
export default loading


export const IsLoading_True = () => ({
    type: D.LOADING_TRUE
})
export const IsLoading_False = () => ({
    type: D.LOADING_FALSE
})