import React from 'react'
import s from './Post.module.css'


const Post= ({
    Post,
    UserImg
}) => {
    return(
        <div className={s.container} id={Post.id}>
        <div className={s.img_con}>
            <img src={UserImg} alt="" className={s.img}/>
                <p className={s.p}>
                    {Post.message}
                </p>
            </div>
              <div className={s.like_b}>     
                <span>Like</span> {Post.likesCounts}
              </div>
            
        </div>
    )
}
export default Post