export const userAPI = {
        getUsers(currentPage =1,pageSize=10 ) {
            return profileAPI.getUsers(currentPage, pageSize)
    },
        unfollow_delete(id) {
            return follow_API.unfollow_delete(id)
    },
        follow_post(id) {
            return  follow_API.follow_post(id)
    },   
        getProfile (userId){
            return getProfileAPI.getProfile(userId)
    },
        getLogin(){
            return  instons.get(`/auth/me`)
    },
        updateStatus(status) {
            return updateStatusAPI.updateStatus(status)
    },
        getStatusUser(id){
            return updateStatusAPI.getStatus(id)
    }
}



export const profileAPI = {
    getUsers(currentPage =1,pageSize=10 ) {
        return  instons.get(`users?page=${currentPage}&count=${pageSize}`)
        .then(response => response.data)
    }
}
export const follow_API = {
    unfollow_delete(id) {
        return instons.delete(`/follow/${id}`)
       .then(response=> response.data)
    }, 
    follow_post(id) {
        return  instons.post(`/follow/${id}`)
        .then(response=> response.data)
     }
}
export const getProfileAPI = {
   getProfile (userId){
        return instons.get(`/profile/${userId}`)
        .then(response => status_two_hundred(response))
    }
}
export const updateStatusAPI = {
    getStatus(id){
        return instons.get(`profile/status/`+id)
        },
    updateStatus(status){
        return instons.put(`profile/status`,{status: status});
        }
}

