// HeaderContainer
import React, { useEffect } from 'react'
import s from './HeaderContainer.module.css'
import {connect} from 'react-redux'
import { A_set_User_Data, IsLoading_True, IsLoading_False, getLogin } from '../../../redux/auther_reducer'
import Header from './Header'


function mapStatetoProps(state) {
    return{
        authCook : state.authCook,
    }
    }
const HeaderContainer = props => {

    useEffect(()=>{
              props.getLogin()
    },[])

    return (
      <Header authCook={props.authCook}/>
    )
}
export default connect(mapStatetoProps, { IsLoading_False, IsLoading_True, A_set_User_Data, getLogin })(HeaderContainer)