import React, { useEffect } from 'react'
import s from './Home.module.css'
import Profile from './Profile/Profile'
import Myposts from './Profile/Myposts'

const Home = ({
    Posts,
    R_FU_HOME_addNewPostRed,
    data,
    status,
    updateStatus
}) => {
    
    return(
        <div className={s.home}>
        <Profile data={data} updateStatus={updateStatus} status={status}/>
        <Myposts Posts={Posts}  R_FU_HOME_addNewPostRed={R_FU_HOME_addNewPostRed}  data={data}/>
        </div>
    )
}
export default Home