import React, { memo, useState } from 'react'
// import Filter_Add from './Offer/Filter_Add'
import Offer from './Offer/Offer'
import S from './Offers.module.css'
let styleInput = {
    boxShadow: null,
    placeholder: '',  
    text: ''
}


const Filter_Add = props => {
const [styleIn, setStyleIn] = useState(styleInput)
    return <div className={S.container_Add}>
    <input onClick={()=>setStyleIn({...styleIn, boxShadow:null})} type='text' className={S.input_add} style={styleIn} value={styleIn.text} onChange={(e)=>{setStyleIn({...styleIn, text:e.target.value})}} placeholder={styleIn.placeholder}/>
    <button className={S.button_add} onClick={()=>{props.click(setStyleIn, styleIn)}}>Add</button>
    </div>
}







const Offers = ({arr, boolPacked ,removeLI, click, onblur}) => {
const [text, setText] = useState('')
let all_the_list_true = arr
.filter(e=>e.value.toLowerCase().includes(text.toLowerCase()))
.map((of, index)=> of.packed && <Offer onblur={onblur} index={index} boolPacked={boolPacked} removeLI={removeLI} key={of.id} of={of}/>) 
let all_the_list_false = arr
.filter(e=>e.value.toLowerCase().includes(text.toLowerCase()))
.map((of,index)=> !of.packed && <Offer onblur={onblur} index={index} boolPacked={boolPacked} removeLI={removeLI} key={of.id} of={of}/>) 

return(
    <div className={S.container}>

     <input className={S.search} value={text} placeholder='Search' onChange={(e)=>{setText(e.target.value)}} />
<div className={S.container_todo}>
    <div className={S.active_true}>
    {all_the_list_true}
    </div>

    <div className={S.active_false}>
    {all_the_list_false}
    </div>
    </div>
    { !text && <Filter_Add click={click} />}

    </div>
)
}




export default memo(Offers)