import { connect } from 'react-redux'
import { 
    R_FU_SetUsers, 
    R__FU_CURRENT_PAGE,
    getUsersThanksCreator,
    follow_Thank,
    unfollow_Thank,
} from '../../redux/Reduser/MainReduser/SearchReduser'
import React, {useEffect} from 'react'
import Search from './Search'
import LoadingHOC from '../Comon/loadingHOC'
import { Redirect } from 'react-router-dom'
import LoginRedirect_HOC from '../Comon/LoginRedirect_HOC'
import {compose} from 'redux'


const SearchAPIcontainer = props => {
    useEffect(()=>{
        
    props.getUsersThanksCreator(props.currentPage, props.pageSize)
    },[])

    const F_onPageChange = (pageNumber) => {
        props.getUsersThanksCreator(pageNumber, props.pageSize)
    }
    if (props.authCook.isAuth === false) return  <Redirect to={'/Login'}/>
    return( 
    <>
    {props.isLoadingSearch ? 
 <Search 
 F_onPageChange={F_onPageChange} 
 Users={props.Users} 
 currentPage={props.currentPage}
 totalUsersCount={props.totalUsersCount}
 pageSize={props.pageSize}
 followingPogres ={props.followingPogres}
 follow_Thank={props.follow_Thank}
 unfollow_Thank={props.unfollow_Thank}
    />    : <LoadingHOC />
} </>
    )} 
    
function mapStatetoProps(state) {
    return{
        Users : state.SearchReduser.Users,
        pageSize : state.SearchReduser.pageSize,
        totalUsersCount : state.SearchReduser.totalUsersCount,
        currentPage : state.SearchReduser.currentPage,
        isLoadingSearch : state.SearchReduser.isLoadingSearch,
        followingPogres : state.SearchReduser.followingPogres,
        authCook : state.authCook
    }
    }

export default  compose(
    connect(mapStatetoProps, {
        R_FU_SetUsers,
        R__FU_CURRENT_PAGE,
        getUsersThanksCreator,
        follow_Thank,
        unfollow_Thank
        }
    ),
    LoginRedirect_HOC
)(SearchAPIcontainer)



