import React from 'react'
// import LookingForName from './componen/LookingForName'
import NamePerson from './componen/NamePerson'
import s from './Message.module.css'

const Message= (props) => {
let persons = props.Username.map(user=> <NamePerson key={user.id} id={user.id} name={user.name} newMessageText={user.newMessageText} UserMess={user.UserMess} P_F_SendNewMessagge={props.P_F_SendNewMessagge} P_F_Message_newMessageText={props.P_F_Message_newMessageText}/>);
    return(
        <div className={s.MainMessage}>
        {/* <div className={s.containerLookingFor}> */}
             {/* <LookingForName /> */}
        {/* </div> */}
        <div className={s.container}>
            <div className={s.namePersonContainer}>
 {persons}
            </div>
            <div className={s.messagePersonContainer}>
            </div>
        </div>
        </div>
    )
}
export default Message
