import React, { useState } from "react";
import S from "./Settings.module.css";

const style_Ap_S = {
    black: { color: 'white',
             backgroundColor: 'rgba(31, 31, 31, 0.993)',
   },
     white:{
       color: 'rgb(0, 0, 0)',
       backgroundColor: 'darkgray'
     }
   }

   const detailSetting = {
    color: 'black',
    backgroundColor: 'white'
   }

const Settings = (props) => {
const [det_color, setDet_color] = useState(detailSetting)
const onChanDet_style = ({target}) => {
    setDet_color({...det_color, [target.name]: target.value})
}
  return (
    <div className={S.setting}>
      <div className={S.fast_style}>
        <button onClick={()=>{props.choice_style(style_Ap_S.black)}} className={S.button}>dark theme</button>
  
        <button onClick={()=>{props.choice_style(style_Ap_S.white)}} className={S.button}>light theme</button>
      </div>
      <div className={S.global_detail}>
        <div className={S.detail_st}>
          <p>Color text</p>
          <input onChange={(e)=>onChanDet_style(e)} type="color" name='color'/>
        </div>
        <div className={S.detail_st}>
          <p>Color body</p>
          <input onChange={(e)=>onChanDet_style(e)} type="color" name='backgroundColor'/>
        </div>
      </div>
      <button onClick={()=>{props.choice_style(det_color)}} className={S.button}>confirm</button>
    </div>
  );
};
export default Settings;
