import Home from './Home'
import {connect} from 'react-redux'
import {R_FU_HOME_addNewPostRed, 
  R_FU_HOME_OPEN_USER_HOME_PAGE,  
  R__FU_H_IsLoading_False,
  R__FU_H_IsLoading_True, 
  getProfile,
  getStatus,
  updateStatus} from '../../redux/Reduser/MainReduser/HomeReduser'
import React, { useEffect } from 'react'
import LoadingHOC from '../Comon/loadingHOC'
import { withRouter } from 'react-router-dom'
import {compose} from 'redux'
function mapStatetoProps(state) {
return{
    Posts: state.homedata.Posts,
    isLoading : state.homedata.isLoading,
    data : state.homedata.data, 
    status : state.homedata.status,   
    authCook : state.authCook

  }
}
const HomeContainer = props => {

  
  useEffect(()=>{
    let userId = props.match.params.userId ? props.match.params.userId : props.authCook.userId ? props.authCook.userId : 13125;
    props.getStatus(userId)
    props.getProfile(userId)

    },[HomeContainer]);
    
  return props.isLoading ? <Home   {...props}  /> : <LoadingHOC />
}


export default   compose(
  connect(mapStatetoProps, 
    {R_FU_HOME_addNewPostRed, 
    R_FU_HOME_OPEN_USER_HOME_PAGE, 
    R__FU_H_IsLoading_False, 
    R__FU_H_IsLoading_True, 
    getProfile,
    getStatus,
    updateStatus}),
      withRouter,
)(HomeContainer)

