import Message from './Message'
import { R_FU_Message_newMessageSend, R_FU_Message_newMessageText } from '../../redux/Reduser/FMessage'
import { connect } from 'react-redux';

function mapStatetoProps(state) {
    return{
        Username: state.alltheReduser.dataUser.messagePage.UserFriends,
    }
    }

function mapDispatchToProps(dispatch) {
    return {
        P_F_SendNewMessagge:(id)=>{
            dispatch(R_FU_Message_newMessageSend(id))
        },
        P_F_Message_newMessageText: (text, id)=>{
            dispatch(R_FU_Message_newMessageText(text, id))
        }

    }
}    
    
export default   connect(mapStatetoProps, mapDispatchToProps)(Message)
