import React, { useEffect, useState } from 'react'
import Offers from './Offers'
import { generate as id } from "shortid";
import { connect } from 'react-redux';
import { compose } from 'redux';
import {removeLI, boolPacked, click_add, onblur} from '../../redux/My_corect/offer'



let arrElem = [
    { value: "Pants", id: id(), packed: false, filter: false},
    { value: "Jacket", id: id(), packed: false, filter: false},
    { value: "iPhone Charger", id:id(), packed: false, filter: false},
    { value: "MacBook", id:id(), packed: false, filter: false},
    { value: "Sleeping Pills", id:id(), packed: true, filter: false},
    { value: "Underwear", id:id(), packed: false, filter: false},
    { value: "Hat", id:id(), packed: false, filter: false},
    { value: "T-Shirts", id:id(), packed: false, filter: false},
    { value: "Belt", id:id(), packed: false, filter: false},
    { value: "Passport", id:id(), packed: true, filter: false},
    { value: "Sandwich", id:id(), packed: true, filter: false}
  ];


const OffersContainer = props => {
  const  click = (setT, T) => {
        let texts = T.text.replace(/ +/g, ' ').trim()
       if(texts === '') {
        setT({
            boxShadow: '0px 2px red',
            placeholder: 'Это поле не может быть пустым', 
            text:'' 
        })
       } else {
        props.click_add(T.text)
        setT({
            boxShadow: null,
            placeholder: '', 
            text: '' 
        })
       }
   }

    return <Offers 
    arr={props.Todolist} 
    boolPacked={props.boolPacked} 
    removeLI={props.removeLI}
    click={click}
    onblur={props.onblur}
     />

}



function mapStatetoProps(state) {
    return{
        Todolist : state.Todolist
    
      }
    }

    export default connect(mapStatetoProps,{removeLI, boolPacked,click_add, onblur})(OffersContainer)