import React, { useEffect, useState } from 'react'
import S from './SearchUser.module.css'
import imgUser from '../../../img/imgUser/User_Img.jpg'
import { NavLink } from 'react-router-dom'
const SearchUser = ({
    User,
    followingPogres,
    follow_Thank,
    unfollow_Thank
}) => {
    const MainimgUser =  User.photos.small  ? User.photos.small : imgUser 
    const [activ_but, setActiv_but] = useState(false)
    const class_active_but = activ_but ? S.activ_true : S.activ_false;
    const clikc_anim = e =>{
      setActiv_but(true)
   setTimeout( ()=>{
    setActiv_but(false)
   },180)
    }
    return(
        <div id={User.id} className={S.user}>
         <NavLink to={'/Home/'+User.id}><img src={MainimgUser} alt="" className={S.imgUser}/></NavLink>
           <div className={S.name}>{User.name}</div> 

           {User.followed 

?<button
// className={S.button}
className={class_active_but}
 disabled={followingPogres.some(id=> id === User.id)}
 onClick={()=>{
  unfollow_Thank(User.id)
  clikc_anim()
}}>Unfollow</button>


: <button 
// className={S.button}
className={class_active_but}
disabled={followingPogres.some(id=> id === User.id)} 
onClick={()=>{
  follow_Thank(User.id)
  clikc_anim()
}}>Follow</button>

}
            <div>
            {/* <span>Like</span> {Post.likesCounts} */}
            </div>
        </div>
    )
}
export default SearchUser