import {combineReducers, applyMiddleware} from 'redux'
import alltheReduser from './Reduser/MainReduser/alltheReduser';
import HomeReduser from './Reduser/MainReduser/HomeReduser';
import MessageReduser from './Reduser/MainReduser/MessageReduser';
import SearchReduser from './Reduser/MainReduser/SearchReduser';
import auther_reducer from './auther_reducer'
import loading from './loading/loading'
import {createStore} from 'redux'
import thunkMiddleware from "redux-thunk"
import {reducer as formReducer} from 'redux-form'
import Todolist from './My_corect/offer';

const allReducers = combineReducers({
    homedata: HomeReduser,
    messagedata: MessageReduser,
    alltheReduser,
    SearchReduser,
    authCook: auther_reducer,
    loading,
    form: formReducer,
    Todolist
})


const store = createStore(allReducers, applyMiddleware(thunkMiddleware)); 

window.store = store;

export default store

