import React from 'react'

import {Route, Switch} from "react-router-dom"
import Payment from './Components/Payment/Payment'
import Hot_Offers from './Components/Hot_Offers/Hot_Offers'
// import Offers from './Components/Offers/Offers'
import Gallery from './Components/Gallery/Gallery'
import Settings from './Components/Settings/Settings'
import Top_Users from './Components/Top_Users/Top_Users'
import MessageContainer from './Components/Message/MessageContainer'
import HomeContainer from './Components/Home/HomeContainer'
import SearchContainer from './Components/Search/SearchContainer'
import LoginReduxForm from './Components/Login/LoginContainer'
import OffersContainer from './Components/Offers/OffersContainer'
import S from './BodyComponents.module.css'

const BodyComponents = props => {
    return (
        <div className={S.body_global_container}>
        <Switch>
        {/* <Route exact path="/" render={()=><Home/>}/> */}
        <Route path="/Home/:userId?" component={HomeContainer}/>
        <Route path="/Payment" component={Payment} />
        {/* <Route path="/Message" render={()=><Message messagedata={messagedata}/>}/> */}
        <Route path="/Message" component={MessageContainer}/>
        <Route path="/Hot_Offers" component={Hot_Offers} />
        <Route path="/Offers" component={OffersContainer}/>
        <Route path="/Search" component={SearchContainer}/>
        <Route path="/Gallery" component={Gallery}/>
        <Route path="/Settings"  render={()=><Settings choice_style={props.choice_style}/>} /> 
        <Route path="/Top_Users" component={Top_Users}/>
        <Route path="/Login" component={LoginReduxForm}/>
        </Switch>
        </div>
     );
}

export default BodyComponents

